import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import GroupsIcon from '@mui/icons-material/Groups';
import PeopleIcon from '@mui/icons-material/People';
import FreeBreakfastIcon from '@mui/icons-material/FreeBreakfast';
import LocalFloristIcon from '@mui/icons-material/LocalFlorist';
import FavoriteIcon from '@mui/icons-material/Favorite';
import HomeIcon from '@mui/icons-material/Home';
import PersonIcon from '@mui/icons-material/Person';
import { LEGENDS } from './config';
// import WorkIcon from '@mui/icons-material/Work';

const data = [
  {id:1, type:'C', title:'', desc:'',},
  {id:2, type:'C', title:'', desc:'',},
  {id:3, type:'A', title:'', desc:'(14/2)',},
  {id:4, type:'A', title:'', desc:'',},
  {id:5, type:'A', title:'', desc:'',},
  {id:6, type:'C', title:'', desc:'(16/3)',},
  {id:7, type:'C', title:'', desc:'',},
  {id:8, type:'S', title:'', desc:'',},
  {id:9, type:'C', title:'', desc:'',},
  {id:10, type:'C', title:'', desc:'',},
  {id:11, type:'S', title:'', desc:'',},
  {id:12, type:'A', title:'', desc:'',},
  {id:13, type:'C', title:'', desc:'',},
  {id:14, type:'C', title:'', desc:'',},
  {id:15, type:'G', title:'', desc:'',},
  {id:16, type:'C', title:'', desc:'',},
  {id:17, type:'C', title:'', desc:'',},
  {id:18, type:'Y', title:'', desc:'',},
  {id:19, type:'S', title:'', desc:'',},
  {id:20, type:'A', title:'', desc:'',},
  {id:21, type:'C', title:'', desc:'',},
  {id:22, type:'A', title:'', desc:'',},
  {id:23, type:'A', title:'', desc:'()',},
  {id:24, type:'S', title:'', desc:'',},
  {id:25, type:'F', title:"/", desc:'',},
  {id:26, type:'G', title:"", desc:'',},
  {id:27, type:'C', title:"1", desc:'',},
  {id:28, type:'C', title:"4-9", desc:'',},
  {id:29, type:'I', title:'', desc:'',},
  {id:30, type:'I', title:'', desc:'',},
  {id:31, type:'I', title:'', desc:'',},
];

interface EventProps {
  id: number;
}

export default function Event({id}: EventProps) {
  var index;
  var type = '';
  var title = '';
  var desc = '';
  var color = '';

  for (index in data) {
    if (data[index].id === id) {
      type = data[index].type;
      title = data[index].title;
      desc = data[index].desc;
      break;
    }
  }

  for (index in LEGENDS) {
    if (LEGENDS[index].type === type) {
      color = LEGENDS[index].color;
      break;
    }
  }

  return (
    <List dense sx={{mb:0, pb:0,}}>
      <ListItem disablePadding alignItems='flex-start'>
        <ListItemIcon sx={{minWidth:'22px', mt:'5px',}}>
            {type === 'A' && 
              <GroupsIcon fontSize='small' htmlColor={color} />
            }
            {type === 'G' && 
              <PeopleIcon fontSize='small' htmlColor={color} />
            }
            {type === 'I' && 
              <FreeBreakfastIcon fontSize='small' htmlColor={color} />
            }
            {type === 'Y' && 
              <LocalFloristIcon fontSize='small' htmlColor={color} />
            }
            {type === 'C' && 
              <FavoriteIcon fontSize='small' htmlColor={color} />
            }
            {type === 'F' && 
              <HomeIcon fontSize='small' htmlColor={color} />
            }
            {type === 'S' && 
              <PersonIcon fontSize='small' htmlColor={color} />
            }
        </ListItemIcon>

        {desc !== '' &&
          <ListItemText 
            primary={title}
            secondary={desc}
            primaryTypographyProps={{ 
              // variant: 'subtitle1', 
              style: {
                  // whiteSpace: 'nowrap',
              }
            }}
            secondaryTypographyProps={{ 
              // variant: 'subtitle2', 
              style: {
                  // whiteSpace: 'nowrap',
                  fontSize: '0.8rem',
              }
            }}          
          />
        }

        {desc === '' &&
          <ListItemText 
            primary={title}
            primaryTypographyProps={{ 
              // variant: 'subtitle1', 
              style: {
                  // whiteSpace: 'nowrap',
              }
            }}       
          />
        }
      </ListItem>
    </List>
  );
}
